.services__row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

@media only screen and (max-width: 1023px) {
  .services__row {
    flex-wrap: wrap;
  }
}

.services__col {
  position: relative;
  z-index: 2;
}

.services__col.services__col_flex {
  flex: 1 1 65%;
}

.services__col.col-flex {
  display: flex;
  flex-wrap: wrap;
}

@media only screen and (max-width: 1023px) {
  .services__col.col-flex {
    flex-direction: column;
  }
}

.services__title {
  position: relative;
  z-index: 2;
  max-width: 290px;
  text-transform: uppercase;
  letter-spacing: -1px;
  color: #FFF218;
  padding: 20px;
}

.services__image {
  position: absolute;
  top: 80px;
  left: 39%;
  width: 360px;
  height: 0;
  padding-bottom: 27%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  z-index: 1;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
}

.services__image:after {
  position: absolute;
  top: 0;
  left: 0;
  content: '';
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
}

@media only screen and (max-width: 767px) {
  .services__image {
    top: 210px;
    left: 0;
    width: 280px;
    height: 150px;
  }
}

.services__contain {
  position: relative;
  max-width: 650px;
  margin: 180px auto 0;
  z-index: 2;
}

.services__button {
  margin-top: 30px;
}

@media only screen and (max-width: 767px) {
  .services__button {
    margin-top: 10px;
  }
}

.services__button .btn {
  color: #000;
  transition: color .3s;
  white-space: nowrap;
  text-align: center;
}

.services.is-show .button__btn {
  color: #fff;
  transition-delay: 1.2s;
}

.animate-bg-black {
  position: absolute;
  right: -100px;
  bottom: 0;
  display: block;
  background-color: #000;
  width: 320%;
  height: 100%;
  transform: translateX(-100%);
  z-index: 1;
}

.block_mod p {
  font-weight: 500;
  color: #000;
}

@media only screen and (max-width: 767px) {
  .block_mod p {
    padding-left: 0;
  }
}

.gallery__wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 100px 0 160px;
}

@media only screen and (max-width: 767px) {
  .gallery__wrap {
    padding: 50px 0 80px;
  }
}

.gallery__row {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.gallery__row.gallery__row_position {
  position: relative;
  margin-top: -70px;
}

@media only screen and (max-width: 767px) {
  .gallery__row.gallery__row_position {
    margin-top: 50px;
  }
}

.gallery__col {
  position: relative;
}

.gallery__col.gallery__col_flex {
  flex: 1 1 65%;
}

.gallery__title {
  position: relative;
  padding: 20px;
  padding-left: 0;
  max-width: 290px;
  text-transform: uppercase;
  letter-spacing: -1px;
  color: #000;
  z-index: 3;
}

.gallery__button .btn {
  color: #000;
  transition: color .3s;
  white-space: nowrap;
  text-align: center;
}

.gallery__logo {
  z-index: 1;
}

.gallery__logo .icon-logo {
  fill: #d6d6d6;
  font-size: 140px;
}

@media only screen and (max-width: 599px) {
  .gallery__logo {
    display: none;
  }
}

.gallery__slider {
  position: relative;
  top: -20px;
  width: 860px;
  margin: 0 0 0 auto;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
  font-size: 0;
  background-color: #fff;
  z-index: 3;
}

@media only screen and (max-width: 1023px) {
  .gallery__slider {
    width: 600px;
  }
}

@media only screen and (max-width: 767px) {
  .gallery__slider {
    width: 560px;
    top: 20px;
    margin: auto 0 0 0;
  }
}

@media only screen and (max-width: 599px) {
  .gallery__slider {
    width: 280px;
    height: 150px;
  }
}

.animate-bg-yellow {
  position: absolute;
  right: -100px;
  bottom: 0;
  display: block;
  background-color: #FFF218;
  width: 320%;
  height: 100%;
  transform: translateX(-100%);
  z-index: 1;
}

.slider {
  max-width: 100%;
}

.slider__image {
  width: 100%;
  height: 0;
  padding-bottom: 45%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  z-index: 2;
}

@media only screen and (max-width: 599px) {
  .slider__image {
    padding-bottom: 55%;
  }
}

.slider__image:after {
  position: absolute;
  top: 0;
  left: 0;
  content: '';
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
}

.slider .dots-custom {
  position: absolute;
  top: 25px;
  right: 15px;
}

.slider .dots-custom li {
  margin-bottom: 14px;
}

.slider .dots-custom li button {
  font-size: 0;
  width: 12px;
  height: 12px;
  background: none;
  border: 2px solid #FFF218;
  transition: background-color .3s;
}

.slider .dots-custom li.slick-active button {
  background-color: #FFF218;
}

.footer {
  padding: 40px 0;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.75) 100%);
}

.footer__autor {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.3;
  letter-spacing: 1px;
  color: #808080;
  text-transform: uppercase;
}

@media only screen and (max-width: 767px) {
  .footer__autor {
    flex-wrap: wrap;
  }
  .footer__autor span {
    margin-bottom: 15px;
  }
}

.footer__contact {
  margin-bottom: 35px;
}

@media only screen and (max-width: 767px) {
  .nav-list_mod {
    display: none;
  }
}

.nav-list {
  display: flex;
  align-items: center;
  margin-bottom: 35px;
}

@media only screen and (max-width: 1023px) {
  .nav-list {
    flex-wrap: wrap;
  }
}

.nav-list__item {
  position: relative;
  overflow: hidden;
  display: inline-block;
  margin-right: 30px;
}

@media only screen and (max-width: 767px) {
  .nav-list__item:not(:last-child) {
    margin-bottom: 10px;
  }
}

.nav-list__link {
  display: block;
  white-space: nowrap;
}

.nav-list__link .hover-block {
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding: 3px 5px;
  background-color: #FFF218;
  transform: translateX(-101%);
  transition: transform .4s;
}

@media only screen and (max-width: 1023px) {
  .nav-list__link .hover-block {
    display: none;
  }
}

.nav-list__link:hover .hover-block {
  transform: translateX(101%);
}

.nav-list__link {
  font-size: 14px;
  font-weight: 500;
  line-height: 1.3;
  letter-spacing: 1px;
  color: #d6d6d6;
  text-decoration: none;
  transition: color .3s;
}

.nav-list__link:hover {
  color: #FFF218;
}

@media only screen and (max-width: 1023px) {
  .nav-list__link:hover {
    color: #d6d6d6;
  }
}

.contact-list {
  display: flex;
  flex-direction: column;
}

.contact-list__item {
  display: inline-block;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.3;
  letter-spacing: 1px;
  color: #808080;
}

.contact-list__item:not(:last-child) {
  margin-bottom: 15px;
}

.social {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 767px) {
  .social {
    width: 100%;
  }
}

.social__item {
  display: inline-block;
}

.social__item:not(:last-child) {
  margin-right: 20px;
}

.social__link .icon-facebook-logo,
.social__link .icon-vk-logo,
.social__link .icon-twitter-logo,
.social__link .icon-google-plus-logo,
.social__link .icon-odnoklassniki-logo {
  font-size: 16px;
  fill: #d6d6d6;
  transition: fill .3s;
}

.social__link .icon-facebook-logo:hover,
.social__link .icon-vk-logo:hover,
.social__link .icon-twitter-logo:hover,
.social__link .icon-google-plus-logo:hover,
.social__link .icon-odnoklassniki-logo:hover {
  fill: #FFF218;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
